<template>
  <div class="dashboard container-fluid no-gutters">
    <div class="row shadow-sm sticky-top sticky-auto">
      <h5 class="col title-item">性能信息</h5>
    </div>
    <div class="row mt-2">
      <div id="cpu" class="col-md-6"></div>
      <div id="mem" class="col-md-6"></div>
    </div>
    <div class="row shadow-sm sticky-top sticky-auto">
      <h5 class="col title-item">端口信息</h5>
    </div>
    <div class="row net-table" v-if="portForm.length">
      <table class="table">
        <thead class="thead-light">
          <tr>
            <th scope="col">协议</th>
            <th scope="col">地址</th>
            <th scope="col">状态</th>
            <th scope="col">pid/进程</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in portForm" :key="index">
            <th scope="row">{{ item[0] }}</th>
            <td>{{ item[1] }}</td>
            <td>{{ item[3] }}</td>
            <td>{{ item[4] }}</td>
          </tr>
        </tbody>
      </table>
    </div>
    <div class="row emty" v-else>
      <svg
        class="bi bi-cloud-slash"
        width="1em"
        height="1em"
        viewBox="0 0 16 16"
        fill="currentColor"
        xmlns="http://www.w3.org/2000/svg"
      >
        <path
          d="M3.901 6.023A3.5 3.5 0 1 0 3.5 13h7.379l-1-1H3.5a2.5 2.5 0 1 1 .423-4.965l.964.164.031-.16-1.017-1.016zm10.125 5.882a1.5 1.5 0 0 0-.289-2.886L12.7 8.854l.216-1.028a4 4 0 0 0-6.682-3.714l-.707-.708a5 5 0 0 1 8.368 4.626 2.501 2.501 0 0 1 .88 4.621l-.748-.746z"
        />
        <path fill-rule="evenodd" d="M13.646 14.354l-12-12 .708-.708 12 12-.707.707z" />
      </svg>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      cpu: 0,
      MenPercent: 0,
      portForm: [],
      timer: {
        cpu: null,
        mem: null
      }
    };
  },
  methods: {
    createCpu() {
      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(document.getElementById("cpu"));
      if (myChart.getWidth() === 0) {
        myChart.resize();
      }
      myChart.showLoading();
      window.onresize = myChart.resize;
      // 指定图表的配置项和数据
      let data = [];
      let max = Date.now() + 18000;
      let min = Date.now() + 1000;
      var option = {
        title: {
          text: "cpu % 利用率"
        },
        tooltip: {
          show: true,
          trigger: "axis"
        },
        xAxis: {
          type: "time",
          interval: 6000,
          max: max,
          min,
          splitLine: {
            show: true
          }
        },
        yAxis: {
          type: "value",
          max: 100,
          splitLine: {
            show: true
          }
        },
        series: [
          {
            name: "% ",
            type: "line",
            showSymbol: false,
            hoverAnimation: false,
            data
          }
        ]
      };
      let xtimes = 0;
      let date = Date.now();
      // 使用刚指定的配置项和数据显示图表。
      return setInterval(() => {
        xtimes++;
        if (xtimes > 18) {
          option.xAxis.max = option.xAxis.max + 1000;
          option.xAxis.min = option.xAxis.min + 1000;
          // data.splice(0, 1);
        }

        myChart.hideLoading();
        data.push([Date.now(), this.cpu]);
        myChart.setOption(option);
      }, 1000);
    },
    createMem() {
      // 基于准备好的dom，初始化echarts实例
      var myChart = this.$echarts.init(document.getElementById("mem"));
      window.addEventListener("resize", () => {
        myChart.resize();
      });
      if (myChart.getWidth() === 0) {
        myChart.resize();
      }
      myChart.showLoading();

      // 指定图表的配置项和数据
      let totalData = [];
      let useData = [];
      let max = Date.now() + 18000;
      let min = Date.now() + 1000;
      let ymax = null;
      var option = {
        title: {
          text: "进程内存 MB 使用率"
        },
        tooltip: {
          show: true,
          trigger: "axis"
        },
        xAxis: {
          type: "time",
          interval: 6000,
          max: max,
          min,
          splitLine: {
            show: true
          }
        },
        yAxis: [
          {
            type: "value",
            max: ymax,
            splitLine: {
              show: true
            }
          },
          {
            type: "value",
            max: ymax,
            splitLine: {
              show: true
            }
          }
        ],
        series: [
          {
            name: "MB 总大小",
            type: "line",
            showSymbol: false,
            hoverAnimation: false,
            data: totalData
          },
          {
            name: "MB 已使用",
            type: "line",
            showSymbol: false,
            hoverAnimation: false,
            data: useData
          }
        ]
      };
      let xtimes = 0;
      let date = Date.now();
      // 使用刚指定的配置项和数据显示图表。
      return setInterval(() => {
        xtimes++;
        if (xtimes > 18) {
          option.xAxis.max = option.xAxis.max + 1000;
          option.xAxis.min = option.xAxis.min + 1000;
          // data.splice(0, 1);
        }
        option.yAxis[0].max = Math.ceil(this.MenPercent[0]);
        option.yAxis[1].max = Math.ceil(this.MenPercent[0]);
        myChart.hideLoading();
        totalData.push([Date.now(), this.MenPercent[0]]);
        useData.push([Date.now(), this.MenPercent[1]]);
        myChart.setOption(option);
      }, 1000);
    },
    addListenSocket() {
      if (!this.$socket.id) {
        let timeOut = setTimeout(() => {
          this.addListenSocket();
          clearTimeout(timeOut);
        }, 500);
        return;
      }
      this.$socket.emit("cpu", { id: this.$socket.id });
      // 获取port信息
      this.$socket.on(this.$socket.id + "_nets", data => {
        this.portForm = data;
        this.$socket.removeListener(this.$socket.id + "_nets");
      });
      this.$socket.emit("nets", { id: this.$socket.id + "_nets" });
      //end
      this.$socket.on(this.$socket.id + "_cpu", msg => {
        this.cpu = msg.CpuPercent;
        this.MenPercent = msg.MenPercent;
      });
    },
    addListenEchar() {
      this.timer.cpu = this.createCpu();
      this.timer.mem = this.createMem();
    },
    removeListent() {
      this.$socket.emit(this.$socket.id + "_cpu_remove", {
        id: this.$socket.id + "_cpu"
      });
      this.$socket.removeListener(this.$socket.id + "_cpu");
      clearInterval(this.timer.cpu);
      clearInterval(this.timer.mem);
    }
  },
  activated() {
    this.addListenSocket();
    this.addListenEchar();
  },
  deactivated() {
    this.removeListent();
  }
};
</script>

<style lang="scss" scoped>
.dashboard {
  height: calc(100vh - 56px);
  overflow: auto;
  ::-webkit-scrollbar {
    display: none;
  }
}
#cpu,
#mem {
  min-height: 250px;
  min-width: 250px;
}
.net-table {
  overflow: hidden;
  overflow-x: auto;
}
.sticky-auto {
  height: 50px;
  font-weight: bold;
  font-size: 16px;
  .title-item {
    line-height: 50px;
    background-color: #fff;
    font-weight: bold;
  }
}
.emty {
  box-sizing: border-box;
  padding: 50px;
  font-size: 60px;
  display: flex;
  justify-content: center;
  align-content: center;
}
</style>